body {
  margin: 0;
  font-family: 'EB Garamond', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



body{
  font-family: 'Open Sans', sans-serif !important;
  overflow-y: scroll !important; /* Show vertical scrollbar */

}
body.modal-open { /** fixes modal moving background when open*/
  margin-right: 0 !important;
  padding: 0 !important;
}


.navbar, #nav1,#nav2,#nav3{
  font-family: 'Open Sans', sans-serif;
  font-size:large
}

a.nav-link:hover{
  text-decoration: underline;
}

@media screen and (min-width: 800px) { /** set icons for nav menu*/
  #nav1:before{
    font-family: "Font Awesome 5 Free"; 
    font-weight: 900; 
    content: "\f007";
  }
  #nav1:after{
    content: "Employees";
  }
  #nav2:before{
    font-family: "Font Awesome 5 Free"; 
    font-weight: 900; 
    content: "\f0c0";
  }
  #nav2:after{
    content: "Department";
  }
  #nav3:before{
    font-family: "Font Awesome 5 Free"; 
    font-weight: 900; 
    content: "\f1ad";
  }
  #nav3:after{
    content: "Location";
  }
}


@media screen and (max-width: 800px) {
  #nav1:before{
    font-family: "Font Awesome 5 Free"; 
    font-weight: 900; 
    content: "\f007 ";
  }
  #nav1:focus:before{
    font-family: "Font Awesome 5 Free"; 
    font-weight: 900; 
    content: "\f007";
  }
  #nav1:focus:after{
    content: "Employees";
  }

  #nav2:before{
    font-family: "Font Awesome 5 Free"; 
    font-weight: 900; 
    content: "\f0c0";
  }
  #nav2:focus:before{
    font-family: "Font Awesome 5 Free"; 
    font-weight: 900; 
    content: "\f0c0";
  }
  #nav2:focus:after{
    content: "Department";
  }
  #nav3:before{
    font-family: "Font Awesome 5 Free"; 
    font-weight: 900; 
    content: "\f1ad";
  }
  #nav3:focus:before{
    font-family: "Font Awesome 5 Free"; 
    font-weight: 900; 
    content: "\f1ad";
  }
  #nav3:focus:after{
    content: "Location";
  }
}

